.App {
    background-color: #ececec;
}

.App{
    height: 100vh;

}
.row.app-wrapper {
    height: 80vh;
}

.app-wrapper {
    color: white;
}


.users-wrapper{
    height: 70vh;
    overflow-y: scroll;
    scrollbar-width: 0;
    
}

.users-wrapper::-webkit-scrollbar {
    display: none;
}
.del-btn{
    border: none;
    background: transparent;
    font-size: 20px;
    font-weight: 500;
    display: inline;
    color: white;
    transition: color 0.6s ease-in;
}
.del-btn:hover{
    color: red;
}

.contact{
    background-color: #F6EAD3;
    border-radius: 5px;
    margin: 0.6rem;
}

.user-avatar{
    width: 75px;
    height: 75px;

}
.order{
    text-align: right !important;
}

.navbar .navbar-nav .nav-item a{
    color: #bcbfc2;
}

.navbar .navbar-nav .nav-item a{
    background-color: #F6EAD3;
    transition:  background-color,color 0.4s ease-in;
}
.navbar .navbar-nav .nav-item a:hover{
    background-color: #E9D1BF;
    color: white;
}
.navbar .navbar-nav .nav-item a:hover .icon{
    color: white;
}
 
.navbar .navbar-nav .nav-item a.active{
    background-color: #E9D1BF;
    color: white;
}
.navbar .navbar-nav .nav-item a.active .icon{
    color: white;
}

